// CertItem Component
import React from 'react'
import './cert-item.scss'
import {Link} from 'react-router-dom'

function CertItem (props) {
  return (
    <div className="cert">
      <Link to={`/training/${props.url}`} aria-label={props.school}>
        <img src={props.img} alt={props.school} loading="lazy" />
      </Link>
      <div className="cert-desc">
        <p>
          <span className="cert-title">Título</span>
          <span>{props.name}</span>
        </p>
        <p>
          <span className="cert-title">Centro</span>
          <span>{props.school}</span>
        </p>

      </div>
    </div>
  )
}

export default CertItem
